<template>
  <div class="container">
    <h1>Tasks</h1>
    <div class="create-task">
      <input type="text" id="create-task" v-model="text" placeholder="type here ...">
      <button v-on:click="createTask()">Add New Task</button>
    </div>
    <hr>
    <p class="error" v-if="error">{{error}}</p>
    <div class="tasks-container">
      <div class="tasks"
        v-for="(task, index) in tasks"
        :key="task._id"
        :index="index"
        :item="task"
        v-on:dblclick="deleteTask(task._id)"
      >
        {{`${task.dateCreated}`}}
        <p class="text">{{task.task}}</p>
        
      </div>
    </div>
  </div>
</template>

<script>
import service from '../service'

export default {
  name: 'Task',
  data(){
    return{
      tasks: [],
      error: '',
      text: ''
    }
  },
  created(){
    service.getTasks().then((res)=> {
      this.tasks = res.data
    })
  },
  methods: {
    async createTask(){
      await service.addTask(this.text)
      this.tasks = await service.getTasks()
      this.text = ''
    },
    async deleteTask(id){
      await service.deleteTask(id)
      this.tasks = await service.getTasks()
    }
  }
}
</script>

