import axios from 'axios'

const url = 'api/tasklist/'

class service{
    static getTasks(){
        return axios.get(url)
    }
    
    static addTask(task){
        return axios.post(url, {
            task,
        })
    }
    
    static deleteTask(id){
        return axios.delete(`${url}${id}`)
    }
}

export default service